<template>
  <div id="FA_div">
    <h2>商品列表</h2>
    <el-table :data="tableData"
              v-loading="loading"
              style="width: 100%">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left"
                   inline
                   class="demo-table-expand">
            <el-form-item label="商品id">
              <span>{{ props.row.id }}</span>
            </el-form-item>

            <el-form-item label="所属分类">
              <span>{{ props.row.category }}</span>
            </el-form-item>

            <el-form-item label="商品图片">
              <el-image :src="props.row.imgUrl"></el-image>
            </el-form-item>

            <el-form-item label="商品评价">
              <span>{{ props.row.rating }}</span>
            </el-form-item>

            <el-form-item label="商品描述">
              <span>{{ props.row.goodsDesc }}</span>
            </el-form-item>

            <el-form-item label="商品名称">
              <span>{{ props.row.name }}</span>
            </el-form-item>

            <el-form-item label="商品价格">
              <span>{{ props.row.price }}</span>
            </el-form-item>
            <el-form-item label="创建时间">
              <span>{{ props.row.ctime }}</span>
            </el-form-item>
            <el-form-item label="商品销量">
              <span>{{ props.row.sellCount }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>

      <el-table-column label="商品名称"
                       prop="name">
      </el-table-column>
      <el-table-column label="所属分类"
                       prop="category">
      </el-table-column>
      <el-table-column label="商品价格"
                       prop="price">
      </el-table-column>
      <el-table-column label="商品图片"
                       prop="imgUrl">
        <template slot-scope="scope">
          <el-image :src="scope.row.imgUrl"></el-image>
        </template>
      </el-table-column>
      <el-table-column label="商品描述"
                       prop="goodsDesc">
      </el-table-column>
      <el-table-column label="操作"
                       prop="desc">
        <template slot-scope="scope">
          <el-button size="mini"
                     @click="handleEdit(scope.row)">编辑</el-button>
          <el-button @click.native.prevent="deleteRow(scope.$index, tableData)"
                     type="danger"
                     size="mini">
            删除
          </el-button>
        </template>

      </el-table-column>
    </el-table>

    <!-- 翻页 -->
    <el-pagination @size-change="handleSizeChange"
                   @current-change="handleCurrentChange"
                   :current-page="currentPage"
                   :page-sizes="[5, , 10, 15]"
                   :page-size="pageSize"
                   layout="total, sizes, prev, pager, next, jumper"
                   :total="total">
    </el-pagination>
    <div id="edit_box"
         v-show="editShow">
      <el-input placeholder="请输入商品名称"
                v-model="Nname"
                clearable>
      </el-input>
      <el-input placeholder="请输入商品所属分类"
                v-model="Ncategory"
                clearable>
      </el-input>
      <el-input placeholder="请输入商品价格"
                v-model="Nprice"
                clearable>
      </el-input>
      <el-upload class="avatar-uploader"
                 :action="ITEMS_UP_IMG"
                 :show-file-list="false"
                 :on-success="handleAvatarSuccess">
        <img v-if="NimgUrl"
             :src="NimgUrl"
             class="avatar"
             style="width:180px;margin-left:115px">
        <i v-else
           class="el-icon-plus avatar-uploader-icon"></i>
      </el-upload>
      <el-input type="textarea"
                :autosize="{ minRows: 2, maxRows: 3}"
                v-model="NgoodsDesc"
                clearable
                size="medium">
      </el-input>
      <el-button type="primary"
                 @click="editok"
                 style="width:100%">确认修改</el-button>
    </div>
  </div>
</template>

<script>
import { list, del, good_edit, ITEMS_UP_IMG, GET_IMG } from '@/API/apis'//获取账号列表
import { getchtime } from '@/utils/gettime'//时间
export default {
  created () {
    this.ITEMS_UP_IMG = ITEMS_UP_IMG
    //获得列表
    this.List()
  },

  data () {
    return {
      currentPage: 1,
      total: 0,
      tableData: [],
      pageSize: 5,
      // 编辑
      Nname: "",
      Ncategory: "",
      Nprice: 0,
      NimgUrl: "",
      NgoodsDesc: "",
      id: "",
      ITEMS_UP_IMG: "",
      editShow: false,
      // 加载
      loading: true,
    }
  },
  methods: {
    // 获取列表
    List () {
      this.loading = true,

        list(this.currentPage, this.pageSize).then(res => {
          this.total = res.data.total
          this.arr = res.data.data
          for (const obj of this.arr) {
            obj.ctime = getchtime(obj.ctime)
            // let host = "http://127.0.0.1:5000/upload/imgs/goods_img/"
            if (!obj.imgUrl.includes(GET_IMG)) {
              obj.imgUrl = GET_IMG + obj.imgUrl
            }
          }
          this.tableData = this.arr
          this.loading = false
        })
    },
    // 翻页部分
    handleSizeChange (pageSize) {
      this.pageSize = pageSize
      this.List()
    },
    handleCurrentChange (currentPage) {
      this.currentPage = currentPage
      this.List()
    },
    //图片修改

    //编辑
    handleEdit (val) {
      this.id = val.id
      this.Nname = val.name
      this.Ncategory = val.category
      this.Nprice = val.price
      this.NimgUrl = val.imgUrl
      this.NgoodsDesc = val.goodsDesc
      this.editShow = true
    },
    handleAvatarSuccess (res) {
      this.NimgUrl = res.imgUrl
    },
    //确认编辑
    editok () {
      //调用接口
      good_edit(this.Nname, this.Ncategory, this.Nprice, this.NimgUrl, this.NgoodsDesc, this.id).then(res => {
        if (res.data.code == 0) {
          this.editShow = false
          this.List()
        }
      })
    },
    //删除
    deleteRow (index) {
      // rows.splice(index, 1);
      this.$confirm('此操作将永久删除, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        del(this.tableData[index].id, 1).then(res => {
          if (res.data.code == 0) {
            this.$message({
              message: res.data.msg,
              type: 'success'
            });
            this.List()
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    }
  }
}
</script>

<style lang="less" scoped>
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
.el-table {
  max-height: 750px;
  overflow: auto;
  .el-image {
    width: 50px;
    height: 50px;
  }
}
#FA_div {
  position: relative;
  #edit_box {
    background-color: white;
    padding: 30px;
    width: 400px;
    min-height: 300px;
    position: absolute;
    top: 200px;
    left: 350px;
    box-shadow: 0 0 3px #ccc, 0 0 3px #ccc, 0 0 3px #ccc;
  }
}
</style>